@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

@media screen and (max-width: 412px) {
  .sprite-cookieCrumble {
    display: none;
  }
  #cookieAlert {
    background-color: transparent;
  }
  #cookieAlert button {
    margin-top: 10px;
  }
  .cookie-modal {
    margin-top: 0;
    position: fixed;
    width: 100%;
    padding: 5px;
    -webkit-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 5px 10px 0px rgba(0, 0, 0, 0.75);
  }
  .cookie-title {
    font-size: 50px;
    margin-top: 20px;
  }
  .cookie-info {
    margin-bottom: 5px;
    margin-top: 5px;
  }
}
