@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

@media screen and (max-width: 768px) {
  #app .contact-message {
    width: 100%;
  }
}

@media screen and (max-width: 412px) {
  #app .about-info {
    clear: both;
    width: 100%;
  }
  #app .contact-info {
    clear: both;
    width: 100%;
  }
  #app .contact-email {
    width: 100%;
    box-sizing: border-box;
  }
  #app .send-contact-form {
    width: 100%;
  }
}
