  background-position: -5px -5px;
}

.sprite-plus-active {
  width: 32px;
  height: 32px;
  background-position: -47px -5px;
}

#homeBird:hover .iconSprite {
  background-position: -141px -5px;
}

#homeCat:hover .iconSprite {
