@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.mobile-info-block {
  display: none;
}

.openNavigation {
  margin-left: 260px !important;
}

.clickNavigationOpen {
  background-image: url(../images/faq-spritesheet.png) !important;
  background-repeat: no-repeat;
  display: block;
  width: 28px  !important;
  height: 46px  !important;
  background-position: -82px -5px  !important;
}

@media screen and (max-width: 1024px) {
  #app #clickNavigation {
    background-image: url("../images/navigation.png");
    background-repeat: no-repeat;
    display: block;
    width: 28px;
    height: 22px;
    background-position: -5px -87px;
    position: absolute;
    top: 10px;
    left: 10px;
    z-index: 100;
  }
  #app .navigation-bar .sprite-Menu-hmburger-icon {
    display: none;
  }
  #app body {
    overflow-x: hidden;
  }
  #app .navigation-bar {
    left: -100%;
    position: absolute;
  }
  #app .navigation-bar {
    position: fixed;
  }
  #app .content {
    width: 100%;
    transition: margin 0.2s;
  }
  #app .navigation-options #hamburger {
    display: none;
  }
  #app .content {
    margin: 0;
  }
  #app .hiddenNavigationOpen {
    left: 0;
  }
  #app .content-store {
    width: 765px;
  }
  #app .hot_items .hot-item {
    margin-right: 1%;
    width: 32.6%;
  }
  #app .hot_items .hot-item:nth-child(3) {
    display: none;
  }
  #app .hot_items .hot-item:last-child {
    margin-right: 0px;
  }
  #app .hot_items .multiple-borders {
    width: calc(32.6% - 13px) !important;
  }
}

@media screen and (max-width: 768px) {
  #app .content-store {
    width: 613px;
  }
  #app .header-logo {
    width: 30%;
  }
  #app .home-button .sub-title {
    font-size: 17px;
  }
  #app .hot_items {
    margin-top: 40px;
  }
  #app .hot_items .hot-item {
    width: 201px;
    margin-right: 5px;
    height: 173px;
    padding-top: 5px;
  }
  #app .hot_items .hot-item:last-child {
    margin-right: 0px;
  }
  #app .hot_items .hot-item .hot-item-image {
    height: 136px;
  }
  #app .hot_items .hot-item .hot-item-image img {
    width: 100%;
  }
  #app .hot_items .multiple-borders {
    height: 160px;
  }
  #app .hot_items .multiple-borders .hot-item-image {
    height: 123px;
  }
  #app .hot_items .multiple-borders img {
    height: 100%;
    width: auto;
  }
  #app .hot_items .hot-item-info {
    padding-bottom: 5px;
    padding-top: 5px;
  }
  #app .newsletter {
    height: 135px;
    margin-top: 30px;
  }
  #app .newsletter-image {
    width: 45%;
  }
  #app .newsletter-image .newsletter-title {
    font-size: 28px;
  }
  #app .newsletter-image .newsletter-subtitle {
    font-size: 17px;
    margin-top: 10px;
  }
  #app .newsletter-subscribe {
    padding: 20px;
    width: 55%;
  }
  #app .newsletter-subscribe p {
    margin-bottom: 10px;
  }
}

.mobile-hotItems-homePage {
  display: none;
}

@media screen and (max-width: 412px) {
  .hot_items {
    display: none;
  }
  .mobile-hotItems-homePage {
    display: block;
  }
  .visitStoreLink {
    margin-bottom: 30px;
  }
  #app .mySlides {
    height: 175px;
    width: auto;
  }
  #app .content-store {
    width: 290px;
  }
  #app .mobile-info-block {
    display: inline;
  }
  #app .desktop-info-block {
    display: none;
  }
  #app .header-logo {
    width: 70%;
    margin-top: 20px;
  }
  #app .info-block {
    text-align: left;
  }
  #app .home-button {
    width: 50%;
    margin-bottom: 20px;
    border: none;
  }
  #app .homepage-navigation {
    height: auto;
    overflow: hidden;
  }
  #app .hot_items {
    height: auto;
    overflow: hidden;
    margin-bottom: 40px;
  }
  #app .hot_items .title {
    font-size: 20px;
  }
  #app .hot_items .hot-item {
    margin-right: 0px;
    width: 90px;
    height: 95px;
  }
  #app .hot_items .hot-item .hot-item-info {
    display: none;
  }
  #app .hot_items .hot-item .hot-item-image {
    height: 100%;
  }
  #app .hot_items .hot-item .hot-item-image img {
    width: auto;
    height: 100%;
  }
  #app .hot_items .hot-item:nth-child(3) {
    display: block;
  }
  #app .hot_items .multiple-borders {
    display: none;
  }
  #app .hot_items .hot-item-overlay {
    display: none;
  }
  #app .newsletter {
    height: auto;
    overflow: hidden;
  }
  #app .newsletter-image {
    height: 140px;
    width: 100%;
  }
  #app .newsletter-image .newsletter-title {
    margin-top: 10px;
  }
  #app .newsletter-subscribe {
    width: 100%;
  }
}
