@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

@media screen and (max-width: 1024px) {
  #app .overlay-content {
    width: 765px;
  }
  #app #search-overlay .overlay-input {
    font-size: 30px;
    background-position: 0px 28px, 410px 30px;
  }
  #app #search-overlay .category {
    padding-bottom: 10px;
  }
  #app #search-overlay .category, #app #search-overlay .price-range {
    width: 65%;
    clear: both;
  }
  #app #search-overlay .category .slider, #app #search-overlay .price-range .slider {
    float: none;
    padding-left: 35px;
    margin-top: 0px;
  }
  #app #search-overlay .category .slider p, #app #search-overlay .price-range .slider p {
    color: #000000;
  }
}

@media screen and (max-width: 768px) {
  #app .overlay-content {
    width: 613px;
  }
  #app #ecape-search, #app #ecape-faq {
    right: 20px;
  }
  #app #search-overlay .category, #app #search-overlay .price-range {
    width: 80%;
  }
  #app #search-overlay .overlay-input {
    margin-top: 40px;
    font-size: 25px;
    background-position: 0px 8px, 350px 12px;
    padding-top: 10px;
    padding-bottom: 10px;
  }
  #app #search-overlay .filterButton {
    padding-left: 40px;
  }
}

#app #mobileFAQTile {
  display: none;
}

@media screen and (max-width: 412px) {
  #app .overlay-content {
    width: 290px;
  }
  #app #notMobileFAQTitle {
    display: none;
  }
  #app #mobileFAQTile {
    display: block;
    margin-top: 50px;
  }
  #app #FAQ-overlay .overlay-content .overlay-input {
    font-size: 22px;
  }
  #app #FAQ-overlay .overlay-content .overlay-item {
    padding: 5px;
  }
  #app #FAQ-overlay .overlay-content .overlay-item .faq-p {
    margin-left: 5px;
  }
  #app #search-overlay .filterButton {
    font-size: 20px;
    padding-left: 0px;
  }
  #app #search-overlay .advanced-dropdown {
    overflow: hidden;
    margin-top: 30px;
  }
  #app #search-overlay .advanced-dropdown #triangle {
    margin-top: 3px;
  }
  #app #search-overlay .advanced-dropdown #triangle.arrow-down {
    margin-top: 6px;
  }
  #app #search-overlay .category {
    width: 100%;
    padding-left: 5px;
    padding-right: 0px;
  }
  #app #search-overlay .price-range {
    width: 100%;
  }
  #app #search-overlay .price-range .slider {
    width: 100%;
    padding: 0px;
    padding-left: 10px;
    padding-right: 12px;
    box-sizing: border-box;
  }
  #app #search-overlay .searchCheckFilter {
    width: 130px;
    margin-right: 20px;
    overflow: hidden;
    float: left;
  }
}
