@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

button.accordion {
  background-color: #717171;
  color: #ffffff;
  font-size: 20px;
  font-family: Arial;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  text-align: left;
  border: none;
  outline: none;
  transition: 0.4s;
  margin-top: 12px;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
/* Style the accordion panel. Note: hidden by default */
.page-faq div.panel {
  padding: 0 18px;
  background-color: #717171;
  max-height: 0;
  overflow: hidden;
  transition: 0.16s ease-in-out;
  opacity: 0;
  padding-left: 60px;
  padding-right: 60px;
  color: #ffffff;
  font-family: Arial;
  font-size: 14px;
}

/* The "show" class is added to the accordion panel when the user clicks on one of the buttons. This will show the panel content */
.page-faq div.panel.show {
  opacity: 1;
  max-height: 700px;
  /* Whatever you like, as long as its more than the height of the content (on all screen sizes) */
}

.faq-arrow {
  float: right;
  margin: 0px;
}
