@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

@media screen and (max-width: 1024px) {
  #app #productDetailLogo {
    width: 25%;
  }
  #app .product-detail-right h1 {
    font-size: 30px;
  }
  #app .product-detail-right .product-detail-item-price {
    margin-bottom: 10px;
  }
  #app .product-detail-right .product-detail-item-colors-title {
    margin-top: 10px;
    margin-bottom: 5px;
  }
  #app .product-detail-right .square, #app .product-detail-right .circle, #app .product-detail-right .triangle {
    display: none;
  }
  #app .passive-product-detail-image {
    height: 70px;
  }
  #app .passive-product-detail-item p {
    margin-bottom: 0px;
  }
  #app .product-detail-item-description-title {
    margin-bottom: 10px;
  }
  #app .product-detail-item-description {
    font-size: 13px;
    margin-top: 5px;
  }
}

#app .responsiveKtag {
  display: none;
  margin-bottom: 20px;
}

@media screen and (max-width: 768px) {
  #app .responsiveKtag {
    display: block;
    margin-bottom: 20px;
  }
  #app .desktopKtag {
    display: none;
  }
  #app .product-detail-left {
    width: 100%;
    clear: both;
    height: 280px;
  }
  #app .product-detail-left .active-product-detail-item {
    width: 80%;
    margin-left: 0;
    display: block;
    float: left;
    height: 100%;
  }
  #app .product-detail-left .passiveItems {
    overflow: hidden;
    position: relative;
    float: left;
    width: 19%;
    margin-left: 1%;
  }
  #app .product-detail-left .passive-product-detail-item {
    width: 100%;
    float: left;
    height: 33.33%;
    overflow: hidden;
    margin-bottom: 3px;
  }
  #app .product-detail-left .passive-product-detail-item .passive-product-detail-image {
    height: 70px;
  }
  #app .product-detail-left .passive-product-detail-item .passive-product-detail-image img {
    height: 100%;
  }
  #app .product-detail-left .passive-product-detail-item p {
    margin-bottom: 0px;
  }
  #app .product-detail-right {
    width: 100%;
    clear: both;
    padding-left: 0px;
  }
  #app .titleAndPrice {
    overflow: hidden;
    width: 80%;
    float: left;
  }
  #app .colorOptions {
    display: block;
    float: right;
    overflow: hidden;
    margin-top: 15px;
  }
}

#app .mobileTitleAndPrice {
  display: none;
}

@media screen and (max-width: 412px) {
  #app #productDetailLogo {
    width: 50%;
  }
  #app .mobileTitleAndPrice {
    display: block;
  }
  #app .mobileTitleAndPrice h1 {
    margin-bottom: 5px;
  }
  #app .mobileTitleAndPrice p {
    font-family: Arial;
    font-size: 26px;
    color: #ffffff;
    margin-top: 0px;
    margin-bottom: 10px;
  }
  #app .notMobileTitleAndPrice {
    display: none;
  }
  #app .product-detail-item {
    overflow-x: hidden;
  }
  #app .product-detail-item .product-detail-left {
    height: auto;
  }
  #app .product-detail-item .product-detail-left .active-product-detail-item, #app .product-detail-item .product-detail-left .passiveItems {
    width: 100%;
  }
  #app .product-detail-item .product-detail-left .active-product-detail-item {
    margin-bottom: 5px;
  }
  #app .product-detail-item .product-detail-left .passiveItems {
    margin: 0px;
  }
  #app .product-detail-item .product-detail-left .passive-product-detail-item {
    margin-bottom: 0px;
    width: 32.6%;
    float: left;
    margin-right: 1%;
    position: relative;
  }
  #app .product-detail-item .product-detail-left .passive-product-detail-item:nth-child(3) {
    margin-right: 0px;
  }
  #app .product-detail-item .product-detail-left .passive-product-detail-item p {
    display: none;
  }
  #app .product-detail-item .product-detail-left .passive-product-detail-item .passive-product-detail-image {
    height: auto;
  }
  #app .product-detail-item .product-detail-left .passive-product-detail-item .passive-product-detail-image img {
    height: auto;
    width: 100%;
    margin-bottom: 0px;
  }
  #app .product-detail-right .colorOptions {
    float: left;
  }
  #app .dimensions-specs {
    overflow: hidden;
    padding-left: 30px;
  }
  #app .tech-info {
    padding-left: 30px;
  }
  #app .product-detail-specs p {
    margin: 5px;
    clear: both;
    font-size: 14px;
  }
  #app .product-detail-specs .specs-title {
    font-size: 20px;
  }
  #app .panel.show {
    padding-left: 20px;
    padding-right: 20px;
  }
}
