@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.overlay-content {
  margin-left: auto;
  margin-right: auto;
  width: 960px;
}

.overlay-content .overlay-title {
  margin-top: 250px;
}

.advanced-filter-content, .product-view-advanced-filter-content {
  clear: both;
  min-height: 280px;
  width: 100%;
}

.advanced-filter-content .category, .product-view-advanced-filter-content .category {
  box-sizing: border-box;
  padding: 35px;
  width: 50%;
  overflow: hidden;
  float: left;
}

.price-range {
  float: left;
  box-sizing: border-box;
  width: 50%;
  min-height: 150px;
}

.advanced-filter-content, .product-view-advanced-filter-content {
  display: none;
}

.advanced-filter-visible {
  display: block;
}

.faq-item-load {
  margin-bottom: 100px;
  position: relative;
}

#FAQ-overlay .loadBar {
  position: fixed;
  bottom: 10px;
  left: 0;
  width: 100%;
}

#FAQ-overlay .loadBar .loader {
  background: url("../images/loadingCirlceGray.png");
}
