@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.overlay-input {
  width: 100%;
  border: none;
  background: transparent;
  border-bottom: 2px solid black;
  padding: 30px;
  box-sizing: border-box;
  font-family: Arial;
  width: 100%;
  padding: 25px;
  font-size: 30px;
  background-image: url(../images/search-icon.png), url("../images/break.png");
  background-repeat: no-repeat;
  display: block;
  background-position: 0px 25px, 330px 30px;
  padding-left: 60px;
}

.overlay-input:focus {
  outline: none;
}

.input-background-type {
  background-image: url(../images/search-icon.png);
  background-position: 0px 25px;
}

#search-overlay .overlay-input {
  color: #e27500;
  font-size: 40px;
  background-position: 0px 30px, 535px 40px;
}

#search-overlay .overlay-input::-webkit-input-placeholder {
  color: #e27500;
  font-weight: bold;
}

.category form {
  font-family: Arial;
  color: #2b2b2b;
}

.category form p {
  font-size: 16px;
  float: left;
  margin: 0px;
  margin-top: 3px;
}

.searchCheckFilter {
  overflow: hidden;
  display: block;
  float: left;
  margin-right: 10px;
}

.squaredOne, .squareOneProductView {
  margin-left: 15px;
  margin-right: 14px;
  width: 15px;
  height: 15px;
  position: relative;
  background: transparent;
  border: 2px solid #9a9a9a;
  float: left;
}

.squaredOne:first-child, .squareOneProductView:first-child {
  margin-left: 0px;
}

.squaredOne label, .squareOneProductView label {
  width: 15px;
  height: 15px;
  position: absolute;
  cursor: pointer;
  left: 0px;
  top: 0px;
}

.squaredOne label:after, .squareOneProductView label:after {
  content: '';
  width: 15px;
  height: 15px;
  position: absolute;
  left: 0px;
  top: 0px;
  background: black;
  opacity: 0;
}

.squaredOne input[type=checkbox], .squareOneProductView input[type=checkbox] {
  visibility: hidden;
}

.squaredOne input[type=checkbox]:checked + label:after, .squareOneProductView input[type=checkbox]:checked + label:after {
  opacity: 1;
}

.slider-input-divider {
  text-align: center;
  float: left;
  display: block;
  width: 33.33%;
  padding-top: 10px;
}

.slider {
  margin-top: 35px;
  margin-right: 20px;
}

.contact-email, .contact-message {
  color: #000000;
  padding: 18px;
  background-color: #ffffff;
  border: 3px solid #9a9a9a;
  font-family: Arial;
  font-size: 16px;
  margin-bottom: 30px;
}

.contact-email::-webkit-input-placeholder, .contact-message::-webkit-input-placeholder {
  font-family: Arial;
  font-size: 16px;
}

.contact-email {
  width: 230px;
}

.contact-message {
  box-sizing: border-box;
  width: 76%;
  height: 140px;
}

#product-view-sort {
  margin-top: 10px;
  margin-bottom: 10px;
  appearance: none;
  -moz-appearance: none;
  /* Firefox */
  -webkit-appearance: none;
  width: 180px;
  padding-left: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  background-color: #616161;
  font-family: Arial;
  font-size: 14px;
  color: #cecece;
  border: none;
  background-image: url("../images/productview-sort-arrow.png");
  background-repeat: no-repeat;
  background-position: 150px 10px;
}

#product-view-sort .product-view-sort-option {
  color: #000000;
  padding-top: 10px;
  padding-bottom: 10px;
  height: 35px;
  background-color: #ffffff;
}

#emailValidationError, #messageValidationError {
  height: 18px;
  color: #ca0000;
  font-family: Arial;
}

#messageValidationError {
  margin-bottom: 20px;
}

.currencyText {
  position: relative;
  overflow: hidden;
  float: left;
  box-sizing: border-box;
  width: 33.33%;
  height: 40px;
}

.currencyText input {
  width: 100%;
  height: 100%;
  padding: 7px;
  box-sizing: border-box;
}

.currencyText span {
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
  left: 15px;
}

#input-number, #input-number-left, #poduct-view-input-number-left, #poduct-view-input-number {
  text-align: right;
}
