@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.clear-button {
  margin-top: 16px;
  float: right;
  font-family: Arial;
  color: #535353;
}

#ecape-faq, #ecape-search {
  position: absolute;
  right: 80px;
  top: 20px;
}

.cookie-modal button {
  background-color: #e27500;
  color: white;
  font-family: Arial;
  font-weight: bold;
  text-align: center;
  padding-top: 18px;
  padding-bottom: 18px;
  width: 100%;
  border: none;
  font-size: 22px;
  margin-top: 20px;
}

.advanced-filter {
  overflow: hidden;
  padding-top: 20px;
  box-sizing: border-box;
}

.filterButton {
  display: block;
  margin: 0px;
  float: left;
}

.arrow-right {
  margin-left: 10px;
  margin-top: 10px;
  float: left;
  width: 0;
  height: 0;
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid #9a9a9a;
}

.arrow-down {
  margin-left: 10px;
  margin-top: 15px;
  float: left;
  width: 0;
  height: 0;
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-top: 10px solid #9a9a9a;
}

#product-view-filter-arrow {
  margin-top: 3px;
}

#product-view-filter-arrow.arrow-down {
  margin-top: 6px;
}

#product-view-filter-arrow.arrow-down {
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-top: 7px solid #9a9a9a;
}

#product-view-filter-arrow.arrow-right {
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: 7px solid #9a9a9a;
}

.advanced-dropdown, .product-view-advanced-dropdown {
  user-select: none;
  cursor: pointer;
}

.send-contact-form {
  background-color: #ff6f9e;
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  color: #ffffff;
  width: 240px;
  border: none;
  font-family: Arial;
  font-size: 19px;
}

.small-circle {
  margin-bottom: 15px;
  width: 15px;
  height: 15px;
  float: left;
  border-radius: 100%;
  border: 1px solid #ffffff;
  margin-right: 5px;
}

.small-circle.white {
  background-color: #ffffff;
}

.small-circle.black {
  background-color: #000000;
}

.small-circle.blue {
  background-color: #2d4aac;
}

.product-detail-right .square, .product-detail-right .circle {
  width: 35px;
  height: 35px;
  float: left;
  margin-right: 10px;
  background-color: #828282;
  border: 1px solid #323232;
}

.product-detail-right .circle {
  border-radius: 100%;
}

.product-detail-right .triangle {
  float: left;
  margin-right: 10px;
  width: 0;
  height: 0;
  border-left: 18px solid transparent;
  border-right: 18px solid transparent;
  border-bottom: 36px solid #323232;
  position: relative;
}

.product-detail-right .triangle:after {
  content: "";
  position: absolute;
  left: -18px;
  top: 0;
  width: 0;
  height: 0;
  border-left: 17.5px solid transparent;
  border-right: 17.5px solid transparent;
  border-bottom: 35px solid #828282;
}

.dogs-button {
  background-color: #ff6f9e;
}

.cats-button {
  background-color: #8787ff;
}

.fish-button {
  background-color: #ffb93f;
}

.birds-button {
  background-color: #ffb93f;
}

.small-animals-button {
  background-color: #7be5ff;
}

.scroll-button {
  position: absolute;
  background-image: url(../images/arrows.png);
  background-repeat: no-repeat;
  display: block;
  top: 50%;
  transform: translate(0, -50%);
  z-index: 30;
  cursor: pointer;
}

#scroll-left {
  right: 5px;
  width: 45px;
  height: 45px;
  background-position: -60px -5px;
}

#scroll-right {
  position: absolute;
  left: 5px;
  width: 45px;
  height: 45px;
  background-position: -5px -5px;
}

.productsCheckFilter {
  margin-right: 20px;
  overflow: hidden;
  float: left;
}

.visitStoreLink {
  font-family: Choplin;
  color: #ffffff;
  font-size: 15px;
  margin-top: 20px;
  float: right;
}

#productDetailLogo {
  top: 0px;
  width: 15%;
}

.clear-button {
  cursor: pointer;
}

.clear-button .clearSymbol {
  background: url("../images/faq-spritesheet.png");
  background-repeat: no-repeat;
  display: block;
  width: 15px;
  height: 15px;
  background-position: -57px -5px;
  float: left;
  margin-right: 3px;
}

.clear-button a {
  float: left;
}
