@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.product-detail-item {
  overflow: hidden;
  margin-top: 140px;
}

.product-detail-left {
  width: 50%;
  float: left;
  padding: 5px;
  box-sizing: border-box;
  background-color: #565656;
}

.product-detail-left img {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.product-detail-right {
  box-sizing: border-box;
  width: 50%;
  float: left;
  padding-left: 20px;
  overflow: hidden;
}

.product-detail-specs {
  margin-top: 20px;
  border-top: 1px solid #717171;
  border-bottom: 1px solid #717171;
  margin-bottom: 50px;
  padding-left: 15px;
}
