@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

.product-view-products-left, .product-view-products-right {
  width: 50%;
  float: left;
  min-height: 100px;
}

.product-view-products-left .hot-item {
  width: 49%;
  margin-bottom: 20px;
  margin-right: 1%;
}

.product-view-products-left .multiple-borders {
  width: calc(49% - 13px) !important;
}

.product-view-products-right {
  background-color: #565656;
  border: 1px solid #717171;
  box-sizing: border-box;
  padding: 5px;
  height: 440px;
}

.product-view-products-right img {
  margin-left: auto;
  margin-right: auto;
}

.product-view-products-right .product-view-active-info {
  padding-right: 15px;
  padding-left: 15px;
  overflow: hidden;
  box-sizing: border-box;
}

.product-view-products {
  width: 100%;
}

.product-view-products-header {
  clear: both;
}
