@font-face {
  font-family: "Choplin";
  src: url("../fonts/Rene Bieder - Choplin Medium-DEMO.otf") format("opentype");
}

@font-face {
  font-family: "Choplin-light";
  src: url("../fonts/32AAC6_0_0.woff") format("woff");
}

@media screen and (max-width: 1024px) {
  #app .product-view-slider-right {
    width: 50%;
  }
  #app .product-view-products-toload .hot-item, #app .product-view-products-left .hot-item {
    height: 160px;
  }
  #app .product-view-products-toload .hot-item .hot-item-image, #app .product-view-products-left .hot-item .hot-item-image {
    height: 120px;
  }
  #app .product-view-products-toload .hot-item .hot-item-image img, #app .product-view-products-left .hot-item .hot-item-image img {
    height: 102px;
  }
  #app .product-view-products-toload .hot-item .hot-item-image .hot-item-overlay, #app .product-view-products-left .hot-item .hot-item-image .hot-item-overlay {
    height: 102px;
  }
  #app .product-view-products-toload .hot-item .hot-item-info, #app .product-view-products-left .hot-item .hot-item-info {
    padding-top: 5px;
    padding-bottom: 5px;
    font-size: 15px;
  }
  #app .product-view-products-toload .multiple-borders, #app .product-view-products-left .multiple-borders {
    height: 147px;
  }
  #app .product-view-products-toload .multiple-borders a .hot-item-image, #app .product-view-products-left .multiple-borders a .hot-item-image {
    height: 98px;
  }
  #app .product-view-products-right {
    height: 338px;
  }
  #app .product-view-products-right img {
    width: 100%;
  }
  #app .product-view-products-right p {
    margin-top: 0px;
    font-size: 12px;
  }
  #app .product-view-products-right .product-view-name {
    margin-top: 0px;
    margin-bottom: 0px;
    font-size: 26px;
  }
  #app .product-view-products-right a {
    padding-top: 5px;
    padding-bottom: 5px;
  }
}

@media screen and (max-width: 768px) {
  #app .product-view-products-left, #app .product-view-products-right {
    display: none;
  }
  #app .product-view-products {
    height: 70px;
  }
  #app .product-view-products-toload {
    min-height: 350px;
  }
  #app .product-view-products-toload .hot-item {
    width: 32.66%;
  }
  #app .product-view-products-toload .hot-item .hot-item-image {
    height: 130px;
  }
  #app .product-view-slider-left {
    width: 390px;
    clear: both;
    padding-right: 0px;
  }
  #app .product-view-slider-right {
    width: 390px;
    clear: both;
    padding-left: 35px;
  }
  #app .product-view-products-toload .hot-item {
    margin-right: 1%;
  }
  #app .product-view-products-toload .hot-item:nth-child(4) {
    margin-right: 1%;
  }
  #app .product-view-products-toload .hot-item:nth-child(3n) {
    margin-right: 0px;
  }
}

@media screen and (max-width: 412px) {
  #app #product-view-sort {
    width: 100%;
  }
  #app .title {
    font-size: 25px;
  }
  #app .productsCheckFilter {
    width: 100%;
  }
  #app .product-view-slider-left {
    padding-right: 11px;
    padding-left: 11px;
    width: 100%;
  }
  #app .product-view-slider-right {
    padding: 0;
    width: 100%;
  }
  #app .product-view-products-toload .hot-item {
    padding-top: 5px;
    width: 100%;
    height: 250px;
    clear: both;
  }
  #app .product-view-products-toload .hot-item .product-view-image {
    height: 210px;
  }
  #app .product-view-products-toload .hot-item .product-view-image img {
    height: 212px;
    max-height: 212px;
  }
  #app .product-view-products-toload .hot-item .product-view-image .hot-item-overlay {
    height: 212px;
  }
  #app .product-view-products-toload .hot-item .hot-item-info {
    padding-top: 10px;
    padding-bottom: 10px;
  }
}
